import React, { useState } from "react";
import { Row, Col, Button, Collapse } from "react-bootstrap";
import PropTypes from "prop-types";

import "../views/Sections/About/About.scss";

const CollapseItem = ({ html }) => {
  const [open, setOpen] = useState(false);

  return (
    <Row className="no-gutter ">
      <Col lg={12}>
        <Collapse in={open}>
          <div className="about-body" id="collapse-text">
            <div dangerouslySetInnerHTML={{ __html: html }} />
          </div>
        </Collapse>
        <div className="text-center" id="collapse-text-button">
          <Button
            onClick={() => setOpen(!open)}
            aria-controls="collapse-text"
            aria-expanded={open}
            className="CollapseBtn mt-2"
          >
            <span className="collapsed">Show More</span>
            <span className="expanded">Show Less</span>
          </Button>
        </div>
      </Col>
    </Row>
  );
};

CollapseItem.propTypes = {
  html: PropTypes.string,
};

CollapseItem.defaultProps = {
  html: "no html linked",
};

export default CollapseItem;
