import React from "react";
import PropTypes from "prop-types";

import { Modal, Button } from "react-bootstrap";
import Image from "components/Image";
import Icon from "./Icon";

const ScreenshotModal = ({ onHide, imageFileName, header, subheader, ...restProps }) => {
  return (
    <Modal
      {...restProps}
      onHide={onHide}
      size="xl"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          <div className="">
            <h1 className="">{header}</h1>
          </div>
        </Modal.Title>
      </Modal.Header>
      <Modal.Body className="w-100">
        <Image className="img-fluid" fileName={imageFileName} alt={header || subheader} />
        <p className="item-intro text-muted text-center mt-4">{subheader}</p>
      </Modal.Body>
      <Modal.Footer>
        <div className="mx-auto">
          <Button variant="primary" onClick={onHide}>
            <Icon iconName="CloseIcon" />
            &nbsp; Close
          </Button>
        </div>
      </Modal.Footer>
    </Modal>
  );
};

ScreenshotModal.propTypes = {
  onHide: PropTypes.func,
  imageFileName: PropTypes.string,
  // imageAlt: PropTypes.string,
  header: PropTypes.string,
  subheader: PropTypes.string,
  date: PropTypes.string,
  content: PropTypes.string,
  // extraInfo: PropTypes.any,
};

ScreenshotModal.defaultProps = {
  onHide: null,
  imageFileName: "",
  // imageAlt: null,
  header: "",
  subheader: "",
  date: "",
  content: "",
  // extraInfo: null,
};

export default ScreenshotModal;
