import React from "react";
import PropTypes from "prop-types";
import clsx from "clsx";

import { Row } from "react-bootstrap";
import SectionHeader from "components/SectionHeader";
import DevlogPostList from "components/DevlogPostList";
import PageSection from "components/PageSection";
import "./Devlog.scss";

const Devlog = ({ className, frontmatter }) => {
  if (!frontmatter) {
    return null;
  }

  const { anchor, header: rootHeader, subheader: rootSubHeader } = frontmatter;

  return (
    <PageSection className={clsx("devlog-section", className)} id={anchor}>
      <Row>
        <SectionHeader header={rootHeader} subheader={rootSubHeader} />
      </Row>
      <Row>
        <DevlogPostList />
      </Row>
    </PageSection>
  );
};

Devlog.propTypes = {
  className: PropTypes.string,
  frontmatter: PropTypes.object,
};

Devlog.defaultProps = {
  className: null,
  frontmatter: null,
};

export default Devlog;
