import React from "react";
import { StaticQuery, graphql } from "gatsby";

// import Image from "components/Image";
// import Icon from "components/Icon";
import PropTypes from "prop-types";
import DevlogPost from "components/DevlogPost";
// import DevlogPostModal from "components/DevlogPostModal";

import "./DevlogPost.scss";

const DevlogPostList = ({
  data: {
    allMarkdownRemark: { edges },
  },
}) => {
  const Posts = edges
    .filter((edge) => !!edge.node.frontmatter.date) // You can filter your posts based on some criteria
    .map((edge) => <DevlogPost key={edge.node.id} post={edge.node} />);

  return <div className="devlog-list">{Posts}</div>;
};

export default function MyDevlogPosts(props) {
  return (
    <StaticQuery
      query={graphql`
        query {
          allMarkdownRemark(
            filter: { fileAbsolutePath: { regex: "/DevlogPosts/" } }
            sort: { fields: frontmatter___date, order: DESC }
          ) {
            edges {
              node {
                id
                frontmatter {
                  title
                  date(formatString: "MMMM DD, YYYY")
                  description
                  imageFileName
                }
                html
              }
            }
          }
        }
      `}
      render={(data) => <DevlogPostList data={data} {...props} />}
    />
  );
}

DevlogPostList.propTypes = {
  data: PropTypes.any,
};

DevlogPostList.defaultProps = {
  data: null,
};
