import React from "react";
import PropTypes from "prop-types";
import Fade from "react-reveal/Fade";

import { Row, Col } from "react-bootstrap";
import Screenshot from "components/Screenshot";
import Video from "components/Video";
import SectionHeader from "components/SectionHeader";
import PageSection from "components/PageSection";

const style = {
  height: "100%",
  width: "auto",
};

const Media = ({ className, frontmatter }) => {
  if (!frontmatter) {
    return null;
  }

  const { anchor, header: rootHeader, subheader: rootSubHeader, screenshots, videos } = frontmatter;

  return (
    <PageSection className={className} id={anchor}>
      <Row>
        <SectionHeader header={rootHeader} subheader={rootSubHeader} />
      </Row>
      <Fade bottom>
        <Row>
          <h2 className="mb-3 pl-3">Screenshots</h2>
        </Row>
        <Row className="no-gutter">
          {screenshots.map(({ imageFileName, title, caption }) => (
            <Col lg={3} md={4} sm={6} key={imageFileName}>
              <Screenshot
                style={style}
                imageFileName={imageFileName}
                title={title}
                caption={caption}
              />
            </Col>
          ))}
        </Row>
      </Fade>
      <Fade bottom>
        <Row>
          <h2 className="mt-5 mb-3 pl-3">Videos</h2>
        </Row>
        <Row className="no-gutter">
          {videos.map(({ videoURL, title, caption }) => (
            <Col lg={4} md={6} key={videoURL}>
              <Video videoURL={videoURL} title={title} caption={caption} />
            </Col>
          ))}
        </Row>
      </Fade>
    </PageSection>
  );
};

Media.propTypes = {
  className: PropTypes.string,
  frontmatter: PropTypes.object,
};

Media.defaultProps = {
  className: null,
  frontmatter: null,
};

export default Media;
