import React from "react";
import PropTypes from "prop-types";
import Slide from "react-reveal/Slide";

import { Row, Col } from "react-bootstrap";
import Icon from "components/Icon";
import PageSection from "components/PageSection";
import SectionHeader from "components/SectionHeader";
import * as SocialIcons from "components/SocialIcons";

import "./Contact.scss";

const Contact = ({ className, frontmatter }) => {
  if (!frontmatter) {
    return null;
  }

  const {
    anchor,
    header,
    subheader,
    email,
    social: { facebook, github, linkedin, medium, twitter, instagram, youtube, discord },
  } = frontmatter;

  return (
    <PageSection className={className} id={anchor}>
      <Row>
        <SectionHeader className="bg-white" header={header} />
      </Row>
      <Slide bottom>
        <Row className="justify-content-center">
          <Col lg={8} className="text-center">
            <hr className="divider my-4" />
            <p className="mb-5 contact-subheader bg-white">{subheader}</p>
          </Col>
        </Row>
      </Slide>
      <Slide bottom>
        <Row className="justify-content-center">
          <Col lg={6} className="text-center mb-5">
            {twitter ? <SocialIcons.Twitter userName={twitter} /> : null}
            {facebook ? <SocialIcons.Facebook userName={facebook} /> : null}
            {linkedin ? <SocialIcons.Linkedin userName={linkedin} /> : null}
            {github ? <SocialIcons.Github userName={github} /> : null}
            {medium ? <SocialIcons.Medium userName={medium} /> : null}
            {instagram ? <SocialIcons.Instagram userName={instagram} /> : null}
            {youtube ? <SocialIcons.Youtube userName={youtube} /> : null}
            {discord ? <SocialIcons.Discord userName={discord} /> : null}
          </Col>
        </Row>
      </Slide>
      <Slide bottom>
        <Row className="justify-content-center">
          {/* <Col lg={4} className="ml-auto text-center">
          <Icon iconName="PhoneIcon" size="3x" className="text-muted mb-3" />
          <a className="d-block" href={`tel:${telephone}`}>
            {telephone}
          </a>
        </Col> */}

          <Col lg={4} className="text-center mt-5">
            <div className="email-us mb-2 bg-white">Email us</div>
            <Icon iconName="EnvelopIcon" size="3x" className="text-muted mb-3" />
            <a className="d-block" href={`mailto:${email}`}>
              <div className="email-text bg-white">{email}</div>
            </a>
          </Col>
        </Row>
      </Slide>
    </PageSection>
  );
};

Contact.propTypes = {
  className: PropTypes.string,
  frontmatter: PropTypes.object,
};

Contact.defaultProps = {
  className: null,
  frontmatter: null,
};

export default Contact;
