import React from "react";
import PropTypes from "prop-types";

import { Card } from "react-bootstrap";
import Image from "components/Image";
import ScreenshotModal from "components/ScreenshotModal";
import "./Screenshot.scss";

const Screenshot = ({ imageFileName, title, caption }) => {
  const [showDetail, setShowDetail] = React.useState(false);
  const handleShowDetail = React.useCallback(() => {
    setShowDetail(true);
  }, []);
  const handleHideDetail = React.useCallback(() => {
    setShowDetail(false);
  }, []);

  return (
    <>
      <div className="pt-2 pb-2">
        <a
          role="button"
          tabIndex={-1}
          className="screenshot-text"
          data-toggle="modal"
          onClick={handleShowDetail}
        >
          <Card className="text-center">
            <div className="card-header pt-2 pb-2 ">{title}</div>
            <Image className="img-thumbnail screenshot" fileName={imageFileName} alt={caption} />
            <div className="card-footer small caption pt-2 pb-2">{caption}</div>
          </Card>
        </a>
      </div>
      <ScreenshotModal
        show={showDetail}
        onHide={handleHideDetail}
        imageFileName={imageFileName}
        header={title}
        subheader={caption}
      />
    </>
  );
};

Screenshot.propTypes = {
  imageFileName: PropTypes.string,
  caption: PropTypes.string,
  title: PropTypes.string,
};

Screenshot.defaultProps = {
  imageFileName: null,
  caption: "",
  title: "",
};

export default Screenshot;
