import React from "react";
import PropTypes from "prop-types";

import { Modal, Button } from "react-bootstrap";
// import Image from "components/Image";
import Icon from "./Icon";

import "./DevlogPostModal.css";

const DevlogPostModal = ({
  onHide,
  imageFileName,
  imageAlt,
  header,
  subheader,
  date,
  content,
  extraInfo,
  ...restProps
}) => {
  return (
    <Modal
      {...restProps}
      onHide={onHide}
      size="xl"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          <div className="small text-muted mt-1">
            <p>{date}</p>
          </div>
          <div className="">
            <h1 className="">{header}</h1>
          </div>
        </Modal.Title>
      </Modal.Header>
      <Modal.Body className="mx-auto pl-4 pr-4">
        <p className="item-intro text-muted">{subheader}</p>

        {/* <Image
          className="img-fluid d-block"
          fileName={imageFileName}
          alt={imageAlt || header || subheader}
        /> */}
        <div
          dangerouslySetInnerHTML={{
            __html: content,
          }}
        />
        {extraInfo}
      </Modal.Body>
      <Modal.Footer>
        <div className="mx-auto">
          <Button variant="primary" onClick={onHide}>
            <Icon iconName="CloseIcon" />
            &nbsp; Close
          </Button>
        </div>
      </Modal.Footer>
    </Modal>
  );
};

DevlogPostModal.propTypes = {
  onHide: PropTypes.func,
  imageFileName: PropTypes.string,
  imageAlt: PropTypes.string,
  header: PropTypes.string,
  subheader: PropTypes.string,
  date: PropTypes.string,
  content: PropTypes.string,
  extraInfo: PropTypes.any,
};

DevlogPostModal.defaultProps = {
  onHide: null,
  imageFileName: "",
  imageAlt: null,
  header: "",
  subheader: "",
  date: "",
  content: "",
  extraInfo: null,
};

export default DevlogPostModal;
