import React from "react";
import PropTypes from "prop-types";
import Fade from "react-reveal/Fade";

import { Col } from "react-bootstrap";
import Image from "components/Image";
// import Icon from "components/Icon";
import DevlogPostModal from "components/DevlogPostModal";

import "./DevlogPost.scss";

const DevlogPost = ({ post }) => {
  const [showDetail, setShowDetail] = React.useState(false);
  const handleShowDetail = React.useCallback(() => {
    setShowDetail(true);
  }, []);
  const handleHideDetail = React.useCallback(() => {
    setShowDetail(false);
  }, []);

  return (
    <>
      <div className="row no-gutters">
        <Col className="devlog-post ">
          <Fade bottom>
            <a
              role="button"
              tabIndex={-1}
              className="devlog-link"
              data-toggle="modal"
              onClick={handleShowDetail}
            >
              <div className="devlog-text">
                <h4>{post.frontmatter.title}</h4>
                <p>{post.frontmatter.date}</p>
                <p>{post.frontmatter.description}</p>
              </div>
              <div className="devlog-hover">
                <div className="devlog-hover-content">
                  {/* <Icon iconName="PlusIcon" size="2x" /> */}
                  <p>Read Me</p>
                </div>
                <div className="devlog-hover-text">
                  <h4>{post.frontmatter.title}</h4>
                  <p>{post.frontmatter.date}</p>
                  <p>{post.frontmatter.description}</p>
                </div>
              </div>
            </a>

            <Image
              className="image devlog-list-image"
              fileName={post.frontmatter.imageFileName}
              alt={post.frontmatter.title || post.frontmatter.description}
            />
          </Fade>
        </Col>
      </div>
      <DevlogPostModal
        show={showDetail}
        onHide={handleHideDetail}
        header={post.frontmatter.title}
        subheader={post.frontmatter.description}
        date={post.frontmatter.date}
        content={post.html}
      />
    </>
  );
};

DevlogPost.propTypes = {
  post: PropTypes.any,
};

DevlogPost.defaultProps = {
  post: null,
};

export default DevlogPost;
